$swatch-margin:10px;

.swatches {
	lost-utility: clearfix;
	lost-center: $site-width;
	padding: $unit 0;

	@include md {
		padding: $unit;
	}
}

.swatch-gallery {
	lost-waffle: 1/6;

	p {
		text-align:center;
		margin-top:3px;
		font-family: $serif-2;
	}

	@include md {
		lost-waffle: 1/4;
	}

	@include sm {
		lost-waffle: 1/3;
	}

	@include xs {
		lost-waffle: 1/2;
	}
}

.description {
	lost-utility: clearfix;;
}

.swatch-name {
	height:35px;
	line-height:14px;
	margin-top:3px;
}
.swatch-list {
	padding-top: gutter();
	margin:0 auto;
	text-align:center;
	lost-utility: clearfix;;
}
.color-info, .color-photo {
	float:left;
}
.color-photo {
	width:200px;
	@include sm {
		width:100%;
		text-align:center;
	}
}
.color-info {
	margin-left:15px;
	width:320px;
	font-size:15px;
	top:50%;
	transform:translateY(-50%);
	position:relative;
	h2 {
		margin-bottom:5px;
		font-size:20px;
	}
	@include sm {
		width:100%;
		margin-left:0;
		height:100%;
		h2 {
			text-align:center;
			margin-top:15px;
		}
	}
}

.s-first-row {
	lost-utility: clearfix;;
	height:200px;
	margin-bottom:15px;
	@include sm {
		width:100%;
		height:100%;
		text-align:center;
	}
}
.s-second-row {
	width:100%;
	.description {
		margin-bottom:0;
	}
}


.blocker {
	position: fixed;
	top: 0; right: 0; bottom: 0; left: 0;
	width: 100%; height: 100%;
	overflow: auto;
	z-index: 1;
	padding: 20px;
	box-sizing: border-box;
	background-color: rgb(0,0,0);
	background-color: rgba(0,0,0,0.75);
	text-align: center;
}
.blocker:before{
	content: "";
	display: inline-block;
	height: 100%;
	vertical-align: middle;
	margin-right: -0.05em;
	@include sm {
		height:30vh;
	}
}
.blocker.behind {
	background-color: transparent;
}

.modal {
	lost-center: 600px;
	border-radius: 8px;
	box-shadow: 0 0 10px #000;
	text-align: left;
	display: inline-block;
	vertical-align: middle;
	position: relative;
	z-index: 2;

	background: #fff;
	padding: 25px;

	.top {
		lost-utility: clearfix;;

		div {
			lost-column: 1/2;

			@include sm {
			width:100%;
			}
		}
	}

	.bottom {

		p {
			width:100%;
		}
	}
}

.modal .prop {
	line-height:40px;

	a {
		padding:5px;
		font-size:12px;
		color:black;
		border:1px black solid;

		&:hover {
			text-decoration:none;
		}
	}
}

.modal a.close-modal {
	position: absolute;
	top: -12.5px;
	right: -12.5px;
	display: block;
	width: 30px;
	height: 30px;
	text-indent: -9999px;
	background: url("data:image/png;base64,") no-repeat 0 0;
	@include sm {
		top:0;
		right:0;
	}

}

.modal-spinner {
	display: none;
	width: 64px;
	height: 64px;
	position: fixed;
	top: 50%;
	left: 50%;
	margin-right: -32px;
	margin-top: -32px;
	background: url("data:image/gif;base64,") #111 no-repeat center center;
	-webkit-border-radius: 8px;
	-moz-border-radius: 8px;
	-o-border-radius: 8px;
	-ms-border-radius: 8px;
	border-radius: 8px;
}